@mixin font-face($font-family, $file-path, $weight: normal, $style: normal) {
    @font-face {
        font-family: $font-family;
        font-style: $style;
        font-weight: $weight;
        src: url('#{$file-path}.eot');
        src: url('#{$file-path}.eot?#iefix') format('embedded-opentype'), url('#{$file-path}.woff') format('woff'), url('#{$file-path}.ttf') format('truetype');
        font-display: swap;
    }
}

// 100 Thin // 200 Extra Light // 300 Light
// 400 Regular // 500 Medium
// 600 Semi Bold // 700 Bold // 800 Extra Bold // 900 Black
@include font-face("Roboto","../fonts/Roboto-Regular/Roboto-Regular",400);
@include font-face("Roboto","../fonts/Roboto-Light/Roboto-Light",300);
@include font-face("ShreeDev0714","../fonts/ShreeDev0714-Regular/ShreeDev0714-Regular",400);
@include font-face("Exo2","../fonts/Exo2-Regular/Exo2-Regular",400);
